html {
  font-size: 62.5%;
  box-sizing: border-box;
  user-select: none;
}

*, *::before, *::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

body {
  font-family: "PT Sans", sans-serif;
  background-color: #141414;
  color: #e5e5e5;
}

a {
  text-decoration: none;
  color: #e5e5e5;
}
a:hover {
  cursor: pointer;
}

.contenedor {
  width: 95%;
  margin: 0 auto;
}

video {
  display: block;
  width: 100%;
  height: auto;
  max-height: 80rem;
  object-fit: cover;
}

button:hover {
  cursor: pointer;
}

.esconder {
  display: none;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem 3rem;
  transition: background-color 0.2s ease;
}
@media (min-width: 480px) {
  .header {
    padding: 2rem 6rem;
  }
}
.header img {
  width: 2rem;
  height: 2rem;
}

.bg-negro {
  background-color: #000;
}

.navegacion {
  display: flex;
  column-gap: 3rem;
  align-items: center;
}
.navegacion .navegacion_logo {
  color: #FF0000;
  text-transform: uppercase;
  font-size: 2.2rem;
}
.navegacion .navegacion-barra {
  display: flex;
  column-gap: 1.8rem;
}
.navegacion .navegacion-barra a {
  font-size: 1.4rem;
}

.nuevo-link {
  display: flex;
  align-items: center;
  column-gap: 1rem;
}
.nuevo-link img {
  width: 1.1rem;
  height: 1.1rem;
}
.nuevo-link p {
  font-size: 1.1rem;
}
@media (min-width: 480px) {
  .nuevo-link p {
    font-size: 1.4rem;
  }
  .nuevo-link img {
    width: 1.5rem;
    height: 1.5rem;
  }
}

.navegacion-extra {
  display: flex;
  column-gap: 2rem;
  align-items: center;
}
.navegacion-extra p {
  font-size: 1.4rem;
}
.navegacion-extra .navegacion-buscar {
  cursor: pointer;
  font-size: 1.4rem;
}
@media (min-width: 480px) {
  .navegacion-extra .navegacion-buscar {
    font-size: 1.6rem;
  }
}
@media (min-width: 768px) {
  .navegacion-extra .navegacion-buscar {
    font-size: 2rem;
  }
}
.navegacion-extra .navegacion-notificaciones {
  cursor: pointer;
  font-size: 2rem;
}
.navegacion-extra .navegacion-perfil {
  display: flex;
  align-items: center;
  column-gap: 0.8rem;
}

.navegacion-perfil-desplegar {
  cursor: pointer;
  transition: 0.2s ease-out all;
  font-size: 1.5rem;
}
.navegacion-perfil-desplegar:hover {
  transform: rotate(180deg);
}

.presentacion-serie {
  position: relative;
}
.presentacion-serie .overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
@media (min-width: 996px) {
  .presentacion-serie .overlay {
    width: 50%;
  }
}

.serie-descripcion {
  padding: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 480px) {
  .serie-descripcion {
    padding: 5rem;
  }
}
.serie-descripcion h1 {
  font-family: "MedievalSharp", cursive;
  font-size: 1.6rem;
  margin: 0;
}
@media (min-width: 480px) {
  .serie-descripcion h1 {
    font-size: 2rem;
  }
}
@media (min-width: 768px) {
  .serie-descripcion h1 {
    font-size: 4rem;
  }
}
.serie-descripcion p {
  font-size: 2rem;
  margin-bottom: 1rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .serie-descripcion p {
    font-size: 3rem;
  }
}
.serie-descripcion .descripcion {
  font-size: 1.2rem;
  font-weight: 400;
}
@media (min-width: 768px) {
  .serie-descripcion .descripcion {
    font-size: 1.6rem;
  }
}

.botones {
  margin-top: 1.5rem;
  display: flex;
  column-gap: 2rem;
}
@media (min-width: 480px) {
  .botones {
    margin-top: 2rem;
  }
}

.boton {
  display: flex;
  align-items: center;
  column-gap: 1rem;
  padding: 0.5rem 1rem;
  font-size: 1rem;
  color: #000;
  font-weight: 700;
  border: none;
  border-radius: 5%;
}
@media (min-width: 480px) {
  .boton {
    padding: 1rem 2rem;
    font-size: 1.2rem;
  }
}
@media (min-width: 768px) {
  .boton {
    font-size: 1.6rem;
  }
}

.boton-transparente-blanco {
  background: rgba(255, 255, 255, 0.7);
}
.boton-transparente-blanco:hover {
  background: rgba(255, 255, 255, 0.4);
}

.boton-transparente-gris {
  background: rgba(109, 109, 110, 0.7);
}
.boton-transparente-gris:hover {
  background: rgba(109, 109, 110, 0.5);
}

.footer {
  width: 95%;
  max-width: 100rem;
  height: auto;
  margin: 0 auto;
  padding: 4rem 0;
  display: flex;
  flex-direction: column;
  row-gap: 2rem;
}
@media (min-width: 996px) {
  .footer {
    padding: 8rem 0 4rem 0;
  }
}

.footer-icons {
  display: flex;
  column-gap: 2rem;
}
.footer-icons .icon-link {
  font-size: 1.5rem;
}
@media (min-width: 480px) {
  .footer-icons .icon-link {
    font-size: 2rem;
  }
}

.lista-ayuda {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}
@media (min-width: 480px) {
  .lista-ayuda {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 768px) {
  .lista-ayuda {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.lista-ayuda a {
  font-size: 1.3rem;
  color: #808080;
}

.footer-codigoservicio {
  width: max-content;
  padding: 1rem;
  font-size: 1.3rem;
  background-color: transparent;
  color: #808080;
  border: 1px solid #808080;
  border-radius: 1%;
}

.copyright {
  font-size: 1.1rem;
  color: #808080;
}
.copyright::before {
  content: "Â© ";
}

.recomendaciones {
  width: 95%;
  margin: -5rem auto 7rem auto;
}
@media (min-width: 768px) {
  .recomendaciones {
    margin-top: -10rem;
    margin-bottom: 12rem;
  }
}

.recomendaciones-encabezado {
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  position: relative;
}
@media (min-width: 480px) {
  .recomendaciones-encabezado {
    padding: 3rem 0;
  }
}
.recomendaciones-encabezado h3 {
  font-size: 1.5rem;
}
@media (min-width: 480px) {
  .recomendaciones-encabezado h3 {
    font-size: 2rem;
  }
}
@media (min-width: 768px) {
  .recomendaciones-encabezado h3 {
    font-size: 2.5rem;
  }
}
.recomendaciones-encabezado h3 .texto-oculto {
  font-size: 1.3rem;
  color: #54b9c5;
  opacity: 0;
}
.recomendaciones-encabezado h3:hover .texto-oculto {
  -webkit-animation: tracking-in-expand 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
  animation: tracking-in-expand 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) both;
  opacity: 1;
}
.recomendaciones-encabezado .indicadores-paginador button {
  background: #e5e5e5;
  height: 0.3rem;
  width: 1rem;
  cursor: pointer;
  border: none;
  margin-right: 0.2rem;
  transition: 0.1s ease all;
  display: none;
}
.recomendaciones-encabezado .indicadores-paginador button:hover {
  background: #FF0000;
}
@media (min-width: 768px) {
  .recomendaciones-encabezado .indicadores-paginador button {
    display: inline;
  }
}

@keyframes tracking-in-expand {
  0% {
    letter-spacing: -0.5em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    opacity: 1;
  }
}
.activo {
  background: #FF0000 !important;
}

.recomendaciones-carrusel {
  display: flex;
  align-items: center;
  position: relative;
}

.carrusel {
  display: flex;
  column-gap: 1rem;
  width: 100%;
  overflow: hidden;
  scroll-behavior: smooth;
}
.carrusel .carrusel-contenido {
  min-width: 45%;
}
@media (min-width: 768px) {
  .carrusel .carrusel-contenido {
    min-width: 30%;
  }
}
@media (min-width: 996px) {
  .carrusel .carrusel-contenido {
    min-width: 22%;
  }
}
@media (min-width: 1200px) {
  .carrusel .carrusel-contenido {
    min-width: 18%;
  }
}
.carrusel .carrusel-contenido img {
  border-radius: 5%;
  width: 100%;
  transition: 0.2s ease all;
  height: auto;
}
.carrusel .carrusel-contenido img:hover {
  transform: scale(1.1);
  z-index: 500;
}

.carrusel-flecha-izquierda,
.carrusel-flecha-derecha {
  font-size: 3rem;
  padding: 0.5rem;
  cursor: pointer;
  position: absolute;
  transition: 0.2s ease-in-out all;
  z-index: 2;
}
.carrusel-flecha-izquierda:hover,
.carrusel-flecha-derecha:hover {
  transform: scale(1.3);
  background: rgba(0, 0, 0, 0.2);
}

.carrusel-flecha-izquierda {
  left: 0;
}

.carrusel-flecha-derecha {
  right: 0;
}